import React from 'react'
import * as styles from '../styles/Home.module.css'

const Interests = ({ interests }) => {

    interests = interests.sort((a, b) => b.weight - a.weight)
    let pyrIntersts = []
    interests.forEach((interest, i) => {
        if (i % 2 === 0) {
            pyrIntersts.push(interest)
        } else {
            pyrIntersts.unshift(interest)
        }
    })
    return (
        <section>
            <h2>My Interests</h2>
            <div className={styles.interests}>
            <ul className={styles.cloud}>
                { pyrIntersts.map(interest => <li data-weight={interest.weight}>{ interest.name }</li>) }
            </ul>
            </div>
        </section>
    )
}

export default Interests